import React from 'react'
import {Line,defaults, Bar } from 'react-chartjs-2'

//defaults.global.legend.position = 'bottom'

const BarChart = (props) => {
    //defaults.global.tooltips.enabled = false
    let con=props.con;

    let arr=[0,0,0];
    arr[0]=props.confirm;
    arr[1]=props.recov;
    arr[2]=props.death;
    let china='china';
  return (
    <div>
      <Bar
        data={{
          labels: ['Infected', 'Recovered', 'Deaths'],
          datasets: [
            {
              label: `current State in ${con}`,
              data:arr,
              backgroundColor: [
                'rgba(255, 99, 132, 0.2)',
                'rgba(54, 162, 235, 0.2)',
                'rgba(255, 206, 86, 0.2)',
                'rgba(75, 192, 192, 0.2)',
                
              ],
              borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
               
              ],
              borderWidth: 1,
            },
          ],
        }}
        height={400}
        width={600}
        options={{
          maintainAspectRatio: false,
          scales: {
            yAxes: [
              {
                ticks: {
                  beginAtZero: true,
                },
              },
            ],
          },
          legend: {
            labels: {
              fontSize: 25,
            },
          },
        }}
      />
    </div>
  )
}

export default BarChart