import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { Fragment } from 'react';
import {useRef} from 'react';
import {useEffect,useState} from 'react'
import Card from './Card.js'
import Chart from './Chart'
import './Head.css'
  var flag=0;
 function Select() {
  const [val,valchange]=useState(''); 
  const[confirm,totconf]=useState('');
  const[death,totdeath]=useState('');
  const[recov,torecov]=useState('');
  const[con,namecon]=useState('');
  const[dat,cdate]=useState('');
  const[err,errorhandle]=useState(false);
  useEffect(() => {   //to get the data directly when the page relaods...
    MovieHandler();
}, [f1]);
async function MovieHandler()
{
        
       const response= await fetch('https://api.covid19api.com/summary');
       const data=await response.json();
       console.log(err);
       if(response.ok==200)
       {
          errorhandle(false);
          
       }   
       console.log(data);
       let m=data;
       //console.log(val);
         for(let i=0;i<190;i++)
         {
          let x=m.Countries[i].Country;

          if(x==(val))
          {
            namecon(m.Countries[i].Country);
              cdate(m.Date);
             console.log(m.Date) 
              totconf(m.Countries[i].TotalConfirmed);
              console.log(m.Countries[i].TotalConfirmed) 
            totdeath(m.Countries[i].TotalDeaths); 
            torecov(m.Countries[i].TotalRecovered);
            break;
          }
         }
}
    const va=useRef();
    function f1(x)
  {
      valchange(x.Country);
      console.log("hello");
  }
  flag++;
  console.log(con);
  return (
    <Fragment>
    <Card con={con} confirm={confirm} death={death} recov={recov} con={con} dat={dat}/>
     <div className="at" >
    <Autocomplete
      onChange={(event, newValue) => {
         console.log(newValue);
         if(newValue==null)
         {
          errorhandle(true);
          return;
          console.log("error");
         } 
         f1(newValue);
      }}
      id="combo-box-demo"
      options={top100Films}
      getOptionLabel={(option) => option.Country}
      style={{ width: 300 }}
      renderInput={(params) => <TextField {...params} label="Country Name" variant="outlined" />}
    />
    </div> 
      <Chart con={con} confirm={confirm} death={death} recov={recov} con={con} dat={dat}/>
    </Fragment>
  );
}
